n;\n  overscroll-behavior-x: contain;\n  overflow: hidden;\n  //overflow-y: auto !important;\n}\n\n#app {\n  width: 100vw;\n  overflow: hidden;\n  overscroll-behavior-y: contain;\n  overscroll-behavior-x: contain;\n\n  * {\n    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);\n\n  }\n\n  .header {\n\n    @include use_hvCenteredContent();\n    position: absolute;\n    top: 30px;\n    left: 250px;\n    height: 50px;\n    right: 0%;\n    //text-align: left;\n    z-index: 11;\n    text-transform: capitalize;\n\n    .description {\n      @include use_hvCenteredContent();\n      font-family: 'Aladin', cursive;\n\n      .logo {\n        height: 50px;\n        margin-left: 20px;\n        display: inline-block;\n      }\n\n      h1 {\n        display: inline-block;\n        margin: 0;\n      }\n\n    }\n  }\n\n  .RadialSelect {\n\n    position: absolute;\n    top: 55px;\n    left: 127px;\n    //right: 0;\n    height: 120px;\n    width: 250px;\n    transform: translateX(-50%);\n    //background: #776112;\n    z-index: 10;\n  }\n\n  .desk {\n    position: absolute;\n    left: 0px;\n    top: 140px;\n    right: 0px;\n    bottom: 0px;\n    display: flex;\n    z-index: 15;\n\n    .menu {\n      width: 250px;\n      text-align: center;\n      padding-top: 50px;\n      overflow-y: auto;\n\n      .button {\n        border: 1px solid green;\n        -webkit-backdrop-filter: blur(10px);\n        backdrop-filter: blur(10px);\n        transform: skewX(-10deg);\n        height: 50px;\n        width: 180px;\n        margin: 10px;\n        border-radius: 20px 5px 20px 0px;\n        position: relative;\n        display: inline-flex;\n        align-items: center;\n        justify-content: center;\n        transition: all 0.3s ease;\n        font: 14px sans-serif;\n        font-weight: 300;\n        text-shadow: 0 0 20px #fff;\n        text-transform: uppercase;\n        -webkit-animation: breath2 2s 0.5s infinite alternate;\n        animation: breath2 2s 0.5s infinite alternate;\n        